.box-quesion {
  position: absolute;
  overflow: hidden;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 999999;
  background: rgba(153, 153, 153, 0.7019607843);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 7px;
  .newStyle {
    width: 100% !important;
    overflow-y: clip !important;
    height: auto !important;
  }
  .styleNotForViwer {
    @media screen and (max-width: 576px) {
      width: 100% !important;
      ul {
        max-height: 240px !important;
      }
    }
  }

  .wrapper-question-vote {
    .withiamge {
      border-bottom-right-radius: 20px !important;
      border-bottom-left-radius: 20px !important;
      border-top-right-radius: 0 !important;
      border-top-left-radius: 0 !important;
      .question-title {
        margin-bottom: 5px !important;
      }
    }
  }

  .wrapper-question-vote {
    width: 33%;
    transition: all 0.3s ease;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    height: 90%;
    border-radius: 20px;
    max-height: 533px;
    max-width: 300px;
    min-width: 200px;
    position: relative;
    .wrapper-two {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      background-color: #dfdfdf;
      justify-content: flex-start;
      border-radius: 20px;
      flex: 3 1;
      height: 65%;
      overflow: hidden;
    }
    .box-img {
      max-width: 700px;
      width: 100%;
      background-color: #dfdfdf;
      // height: 35%;
      img {
        width: 100%;
        height: 100%;
      }
      .upload-file-box {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        min-height: 30%;
        background-color: #dfdfdf;
      }
    }

    .liner-point {
      height: 40px;
      width: 100%;
      background-color: rgb(78 83 96); //rgb(255 160 0)
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 16px;
      position: relative;
      .icon {
        position: absolute;
        left: 25px;
        top: 15px;
        font-size: 2.4rem;
      }
      .icon-close {
        position: absolute;
        left: 20px;
        // top: 20px;
        font-size: 3rem;
        font-weight: bold;
        border: 1px solid #fff;
        color: #fff;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        padding-bottom: 10px;
        padding-left: 1px;
      }
      span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 99;
        font-size: 1.4rem;
        font-family: "Noto Sans JP", Prompt, sans-serif !important;
        font-weight: 400;
        line-height: 1.6;
        color: #fff;
      }
      .layer-duration {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
      }
    }
    .content {
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
      align-items: center;
      padding: 5px 10px 5px;
      gap: 3px;
      text-align: center;
      width: 100%;
      flex: 3 1;

      .wrapper-title {
        // min-height: 47px;
        // max-height: 47px;
        display: flex;
        align-items: center;
        flex: 1;
      }
      .question-title {
        white-space: normal;
        overflow: hidden; /* Prevent text overflow outside the box */
        text-overflow: ellipsis; /* Add ellipsis for truncated text */
        /* Automatically adjust font size to fit the container */
        // font-size: clamp(12px, 1.5vh, 24px); /* Adjust values as needed */
        margin-bottom: 10px !important;
        font-size: 130%;
        margin: 0;
        font-family: "Noto Sans JP", Prompt, sans-serif !important;
        font-weight: bolder;
        color: #333;
      }

      ul {
        width: 100%;
        overflow-y: auto;
        // max-height: 144px;
        max-height: 130px;
        /* padding-bottom: 20px; */
        height: 100%;
        margin: 0;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        min-height: 140px;
        gap: 8px;
        padding-bottom: 5px;

        li {
          cursor: pointer;
          background-color: rgb(245, 245, 245);
          padding: 5px 20px 5px 5px;
          width: 100%;
          border-radius: 8px;
          font-size: 1.5rem;
          font-family: "Noto Sans JP", Prompt, sans-serif !important;
          font-weight: 600;
          line-height: 1.6;
          min-height: 27px;
          position: relative;
          z-index: 999;

          .title {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 99999;
            font-size: 1.4rem;
            font-family: "Noto Sans JP", Prompt, sans-serif !important;
            font-weight: 400;
            line-height: 1.6;
            width: 100%;
          }
          .layer-answer {
            position: absolute;
            left: 0;
            top: 0;
            background-color: rgb(181 181 207 / 70%);
            border-radius: 8px;
            height: 100%;
            width: 0;
            z-index: -1;
            transition: all 0.4s ease;
          }
          .active-layer {
            transition: all 0.4s ease;
          }
          .number {
            position: absolute;
            right: 5px;
            top: 2px;
            font-size: 1.5rem;
            font-family: "Noto Sans JP", Prompt, sans-serif !important;
            font-weight: 700;
            line-height: 1.6;
            color: #d3d3e7;
            span {
              font-size: 1.5rem;
              font-family: "Noto Sans JP", Prompt, sans-serif !important;
              font-weight: 700;
              line-height: 1.6;
              color: #d3d3e7;
            }
          }
        }
      }
    }
  }
}
